export default function getElementType(node) {
  if (!node || !node.type) return '';
  let { type } = node;
  // 组合：node.type + 河图类型 + 卡片类型 + link 展示类型
  const hetuType = node.data?.metadata?.type || '';
  if (hetuType) type += `_${hetuType}`;
  const cardType = node.data?.cardType || '';
  if (cardType) type += `_${cardType}`;
  // HACK: 兼容存量数据
  const displayType = node.data?.cardInfo?.displayType;
  if (displayType) type += `_${displayType}`;
  return type;
}
